<template>
  <div>
      <!-- 这是客户管理 -->
      <div class="title">
        客户管理
      </div>
      <div class="search">
            <!-- <el-form :inline="true" :model="formInline" class="demo-form-inline">
              <el-form-item label="客户公司名称">
                <el-input v-model="formInline.user" placeholder="请输入名称"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="onSubmit">查询</el-button>
              </el-form-item>
            </el-form> -->
            <el-divider></el-divider>
            <!-- 公司表格 -->
            <el-table
              :data="tableData"
              border
              style="width: 100%">
              <el-table-column
                prop="company_name"
                label="公司名称"
                width="150">
              </el-table-column>
              <el-table-column
                prop="company_address"
                label="公司地址">
              </el-table-column>
              <el-table-column
                width="150"
                label="操作">
                <template slot-scope="scope">
                    <el-dropdown>
                    <el-button type="primary">
                        操作<i class="el-icon-arrow-down el-icon--right"></i>
                    </el-button>
                    <el-dropdown-menu slot="dropdown">
                        <!-- <el-dropdown-item command='1'>关联用户信息</el-dropdown-item> -->
                        <el-dropdown-item command='2' @click.native="FnSheBei(scope.row.company_id)">查看用户设备</el-dropdown-item>
                        <el-dropdown-item command='2' @click.native="project(scope.row.company_id)">项目管理</el-dropdown-item>
                        <!-- <el-dropdown-item command='3' @click.native="Fnrepair(scope.row.name)">设备维修记录</el-dropdown-item> -->
                    </el-dropdown-menu>
                    </el-dropdown>
                </template>
              </el-table-column>
            </el-table>
      </div>
  </div>
</template>

<script>
import url from "../../common/api";
export default {
    data(){
        return{
            formInline: {//查询
                user: ''
            },
            tableData: [{//表格查询开始
              date: '2016-05-02',
              name: '王小虎1',
              address: '上海市普陀区金沙江路 1518 弄'
              }, {
              date: '2016-05-04',
              name: '王小虎2',
              address: '上海市普陀区金沙江路 1517 弄'
              }, {
              date: '2016-05-01',
              name: '王小虎3',
              address: '上海市普陀区金沙江路 1519 弄'
              }, {
              date: '2016-05-03',
              name: '王小虎4',
              address: '上海市普陀区金沙江路 1516 弄'
            }]
        };
    },
    methods:{
      onSubmit() {//这是查询筛选
        console.log('点击查询按钮!');
      },
      Fnrepair(i){//这是操作中的-维修记录按钮
        this.$router.push('/bkIndex/bkequipmentList')//跳转设备维修记录列表
        console.log(i,'点击了操作中的-维修记录按钮')
      },
      project(company_id){//去项目
        this.$router.push({
          path:'/bkIndex/bkprojectManagement', //也可以在路径上拼接
          query:{
            company_id       //传递的值
          }
        })
      },
      FnSheBei(company_id){//这是操作中的-查看用户设备
        this.$router.push({
          path:'/bkIndex/bkmanagefacility', //也可以在路径上拼接
          query:{
            company_id       //传递的值
          }
        })
      },
      Fncompanylist(){
        url.companylist()
        .then((res) => {
          console.log(res,'--------------所有激活公司')
          let xx=res.data.data
          let newlist=[]
          xx.forEach(element => {
          element.fields.company_id=element.pk
          newlist.push(element.fields)
          });
          this.tableData=newlist
        });
      }
    },
    mounted(){
      this.Fncompanylist()//所有激活公司
    }

}
</script>

<style lang="stylus" scoped>
.title
    font-size 20px
    color #333
    margin-bottom 20px
    margin-top 20px
    padding-left 15px
    border-left 3px solid #409EFF
    
.search
    margin-left 15px
    margin-top 20px
    margin-right 20px
</style>


